import React, { Component } from "react"
import { connect } from "react-redux"
import { changeCategory } from "../actions/changeCategory"
import { resetEvent } from "../actions/resetEvent"
import { deleteEvent } from "../actions/deleteEvent"
import { resetCategory } from "../actions/resetCategory"
import { customStyles } from "../utilities/customStyles"
import Modal from "react-modal"
import Cookies from "js-cookie"

import ReactCSSTransitionGroup from "react-addons-css-transition-group" // ES6
import { Link } from "gatsby"
import Moment from "react-moment"
import "../main.scss"

import tracker from "./tracker.module.scss"
import Submit from "./submit"
import Add from "./add"

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome"
import { faLongArrowAltRight } from "@fortawesome/free-solid-svg-icons"

class Tracker extends Component {
  constructor() {
    super()
    this.state = {
      modalIsOpen: false,
      selectedEvent: "",
      experience: 2,
      note: "",
      noteError: false,
    }
    this.changeCat = this.changeCat.bind(this)
    this.resetButton = this.resetButton.bind(this)
    this.deleteButton = this.deleteButton.bind(this)
    this.setRating = this.setRating.bind(this)

    this.openModal = this.openModal.bind(this)
    this.afterOpenModal = this.afterOpenModal.bind(this)
    this.closeModal = this.closeModal.bind(this)
    this.handleInput = this.handleInput.bind(this)
  }

  handleInput(e) {
    this.setState({ [e.target.name]: e.target.value, noteError: false })
  }

  openModal(eventId) {
    this.setState({ modalIsOpen: true, selectedEvent: eventId })
  }

  afterOpenModal() {
    // references are now sync'd and can be accessed.
  }

  closeModal() {
    this.setState({ modalIsOpen: false })
  }

  setRating(value) {
    this.setState({ experience: value })
  }
  changeCat(id) {
    this.props.changeCategory(id)
  }

  async resetButton(id) {
    const positiveExp = this.state.experience === 0 ? 1 : 0
    const negativeExp = this.state.experience === 1 ? 1 : 0
    const neutralExp = this.state.experience === 2 ? 1 : 0

    const events = await fetch(process.env.API + "resetEvent", {
      body: JSON.stringify({
        eventId: id,
        positiveExp: positiveExp,
        negativeExp: negativeExp,
        neutralExp: neutralExp,
      }),
      mode: "cors", // no-cors, cors, *include
      cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
      credentials: "include", // include, *include, omit
      headers: {
        "content-type": "application/json",
        "csrf-token": this.props.token,
      },
      method: "POST",
    }).catch(error => this.setState({ isError: true }))
    if (events.ok) {
      const res = await events.json()
      this.setState({ modalIsOpen: false, note: "", noteError: false })
      this.props.resetEvent(res.events)
    } else {
      alert("humhm?")
    }
  }
  async deleteButton(id) {
    if (window.confirm("Are you sure you want to delete this event?")) {
      const token = Cookies.get("XSRF-TOKEN")
      const events = await fetch(process.env.API + "deleteEvent", {
        body: JSON.stringify({
          eventId: id,
        }),
        mode: "cors", // no-cors, cors, *include
        cache: "no-cache", // *default, no-cache, reload, force-cache, only-if-cached
        credentials: "include", // include, *include, omit
        headers: {
          "content-type": "application/json",
          "csrf-token": this.props.token,
        },
        method: "POST",
      }).catch(error => this.setState({ isError: true }))
      if (events.ok) {
        const res = await events.json()
        this.props.deleteEvent(res.events)
      } else {
        alert("humhm?")
      }
    }
  }

  render() {
    const makeNumbers = () => {
      let list = ["Positive", "Negative", "Neutral"]
      let numbers = list.map((e, i) => {
        if (this.state.experience == i) {
          return (
            <div
              onClick={() => {
                this.setRating(i)
              }}
              className={tracker.numTileSelected}
            >
              <h3 className={tracker.rankingSelected}>{e}</h3>
            </div>
          )
        } else {
          return (
            <div
              onClick={() => {
                this.setRating(i)
              }}
              className={tracker.numTile}
            >
              <h3 className={tracker.ranking}>{e}</h3>
            </div>
          )
        }
      })
      return numbers
    }
    const eventCards = this.props.events.map((e, i) => {
      if (e.category == this.props.categories[this.props.category].title) {
        return (
          <div key={i} className={tracker.card}>
            <h3 className={tracker.eventName}>{e.title}</h3>
            <div className={tracker.dataRow}>
              <h2 className={tracker.reset}>Last Occurrence: </h2>
              <h3 className={tracker.since}>
                <Moment fromNow>{e.lastOccurrence}</Moment>
              </h3>
            </div>
            <div className={tracker.dataRow}>
              {" "}
              <h2 className={tracker.reset}>Weekly Count: </h2>{" "}
              <h3 className={tracker.since}>{e.count}</h3>
            </div>
            <div className={tracker.buttonBar}>
              <button
                className={tracker.trackers}
                onClick={() => {
                  this.openModal(e._id)
                }}
              >
                Reset
              </button>
              {/* <button
                className={tracker.trackers}
                onClick={() => {
                  this.resetButton(e._id)
                }}
              >
                Reset
              </button> */}
              <button
                className={tracker.trackers}
                onClick={() => {
                  this.deleteButton(e._id)
                }}
              >
                Delete
              </button>
            </div>
          </div>
        )
      }
    })

    const categoryTabs = this.props.categories.map((c, i) => {
      const style =
        this.props.category === i ? tracker.innerSelected : tracker.innerBlock

      return (
        <div onClick={() => this.changeCat(i)} className={style}>
          <h3 className={tracker.blockTitle}>{c.title}</h3>
        </div>
      )
    })

    return (
      <div className={tracker.box}>
        <div className="content">
          <Add />
          <div className={tracker.navBar}>{categoryTabs}</div>
          {eventCards}
        </div>
        <Modal
          isOpen={this.state.modalIsOpen}
          onAfterOpen={this.afterOpenModal}
          onRequestClose={this.closeModal}
          style={customStyles}
          contentLabel="Example Modal"
        >
          <div className="content">
            <h3 className={tracker.question}>
              How would you rank your experience?
            </h3>
            <div className={tracker.rankingBox}>{makeNumbers()}</div>
            <h3 className={tracker.question}>Add an optional note:</h3>
            {this.state.noteError && (
              <h2 className={tracker.error}>Too Long :(</h2>
            )}
            <textarea
              placeholder="120 characters or less.."
              onChange={this.handleInput}
              type="text"
              name="note"
              value={this.state.note}
              className={tracker.inputSmall}
            />

            <div onClick={this.closeModal} className={tracker.cancelContainer}>
              <h3 className={tracker.submitText}>Cancel</h3>
            </div>
            <div
              onClick={() => {
                this.resetButton(this.state.selectedEvent)
              }}
              className={tracker.submitContainer}
            >
              <h3 className={tracker.submitText}>Submit</h3>
            </div>
          </div>
        </Modal>
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    category: state.activeCategory,
    events: state.events,
    token: state.token,
    categories: state.categories,
  }
}

export default connect(
  mapStateToProps,
  { changeCategory, deleteEvent, resetEvent, resetCategory }
)(Tracker)
